.content__myPost{
    font-size: 20px;
}
.content__myPost>div{
    display: flex;
    flex-direction: column;
    max-width: 800px;
    margin: 10px 0 0 0;
}
.content__myPost textarea{
    background-color: #daf8e9;
    border-radius: 10px;
    height: 50px;
    padding: 5px 10px 5px 10px;
}
.content__myPost button {
    margin: 10px 0 0 0;
    padding: 4px 30px 5px 10px;
    border-radius: 10px;
    align-self: end;
    background-color: aquamarine;
    font-size: 16px;
    text-transform: uppercase;
    color: white;
}
.content__myPost button:active{
    box-shadow: inset 0px 0px 5px 2px gray;
}

/*для постов=======================================*/
.content__profUsers{
    display: flex;
    margin: 0 0 10px 0;
}
.content__profUsers>div{
    position: relative;
    width: 70px;
    height: 70px;
    margin: 0 10px 0 0;
}
.content__profUsers>div>img{
    position: absolute;
    top: 0;
    left: 0;
    max-width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}
